/* Main section styling */
.projects-section {
  background-color: #fff; /* White background */
  color: #000; /* Black text */
  padding: 40px;
  width: 100vw; /* Full viewport width */
  min-height: 60vh; /* Minimum height to cover the full viewport */
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
}

/* Grid layout styling */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 equal columns for TIMEFRAME, DISCIPLINE, TOOLS, and INDUSTRY */
  gap: 50px; /* Space between the grid items */
  align-items: start; /* Align the items to the top */
}

/* Heading styling */
.heading h1 {
  font-size: 4.5rem;
  font-weight: bold;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  opacity: 0; /* Initially hidden */
  transform: translateY(20px); /* Initially positioned below */
  transition: opacity 0.6s ease, transform 0.6s ease; /* Smooth transition */
}

.heading h1.animate {
  opacity: 1;
  transform: translateY(0); /* Move to original position */
}

/* Divider line styling */
.divider {
  border: 0;
  height: 1px;
  background-color: #000; /* Black line */
  margin: 20px 0;
}

/* Category section styling */
.category {
  opacity: 0; /* Initially hidden */
  transform: translateY(20px); /* Initially positioned below */
  transition: opacity 0.6s ease, transform 0.6s ease; /* Smooth transition */
}

.category.animate {
  opacity: 1;
  transform: translateY(0); /* Move to original position */
}

.category h3 {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.category p {
  margin: 5px 0;
}

/* Tag styling */
.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tags span {
  background-color: #fff;
  border: 1px solid #000; /* Black border */
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.9rem;
  font-weight: bold;
  cursor: default;
  transition: background-color 0.3s ease;
}

/* Hover effect for tags */
.tags span:hover {
  background-color: #f4f4f4;
}

/* Media queries for smaller screens */
@media (max-width: 1024px) {
  .projects-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 columns for medium screens */
  }
}

@media (max-width: 768px) {
  .heading h1 {
    font-size: 2rem;
  }

  .projects-grid {
    grid-template-columns: 1fr; /* Single column on smaller screens */
  }

  .projects-section {
    min-height: auto; /* Allow the height to adjust based on content */
  }
}

/* Animation classes for project categories */
.category-enter {
  opacity: 0;
  transform: translateY(20px);
}

.category-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.category-exit {
  opacity: 1;
  transform: translateY(0);
}

.category-exit-active {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

@media (max-width: 768px) {
  .tags {
    display: none; /* Hide only the tags on mobile */
  }
  .industry-heading {
    display: none; /* Hide the heading on mobile */
  }
}